@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rancho&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
*::-webkit-scrollbar {display: none;}
html{
  scroll-behavior: smooth;
}
section {
  width: 100%;
  height: 100%;
}
body { font-family: 'Noto Sans Display', sans-serif }
body.noScroll { overflow-y: hidden }

.overlay {
  position: fixed;
  top: 0;
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
  opacity: 0;
  z-index: -5;
  transition: 0.5s ease-in-out;
}
.overlay.show {
  z-index: 5;
  opacity: 1;
  background: rgba(0, 0, 0, 0.85);
}

.appbar {
  display: flex;
  flex-direction: row;
}
.appbar a {
  text-decoration: none;
  color: white;
  position: relative;
  padding: 10px;
  margin: 50px 100px;
  width: 40px;
  height: 40px;
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.appbar a:hover {z-index: 1;}
.appbar a:before {
  content: "";
  pointer-events: none;
  white-space: nowrap;
  z-index: -1;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 5em;
  opacity: 0;
  font-weight: 500;
  letter-spacing: 500px;
  transition: letter-spacing 0.5s, left 0.5s, right 0.5s, opacity 0.3s;
}
.appbar a:hover:before {
  content: attr(data-text);
  opacity: 1;
  letter-spacing: 10px;
}

.overlay .appbar a{
  margin: 30px;
}
.overlay .appbar a:before {
  font-size: 3.5em;
}

@media (max-width: 992px) {
  .appbar a {
    margin: 50px 30px;
  }
  .appbar a:before {
    font-size: 3.5em;
  }
}
#landing {
  position: relative;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  background: linear-gradient(315deg, #fc9842 0%, #fe5f75 74%);
}
#landing::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 105px;
  background: linear-gradient(to top, #0059b3, transparent);
  z-index: 5;
}
.landingText, .welcomeText {
  position: absolute;
  top: 25%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: "Rancho", cursive;
  color: white;
  letter-spacing: 10px;
  font-size: 3em;
}
.landingText span {
  margin-top: -50px;
  font-size: 5em;
}
.welcomeText {
  top: 57%;
  font-size: 2em;
  letter-spacing: 6px;
  font-weight: 900;
  color: #013b32;
  background: white;
  border-radius: 15px;
  padding: 5px;
  transition: color 0.5s ease-in-out, background 0.5s ease-in-out;
}
.authBtn {
  position: absolute;
  top: 60%;
  text-decoration: none;
  padding: 8px 30px;
  background: white;
  color: #013b32;
  font-size: 1.2em;
  font-weight: 500;
  letter-spacing: 2px;
  border-radius: 40px;
  transition: color 0.5s ease-in-out, background 0.5s ease-in-out;
}
.authBtn:hover, .welcomeText:hover {
  background: #013b32;
  color: white;
}
#landing img {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  pointer-events: none;
}
#landing img#mountainsFront {
  bottom: 100px;
}
#landing img#mountainsBack {
  bottom: 120px;
}
#landing img#birdL,
#landing img#birdR {
  bottom: -500px;
}
#landing img#birdL {
  left: 50%;
}
#landing img#birdR {
  left: -50%;
}
#landing img#forestFront {
  bottom: 100px;
}
#landing img#beach {
  height: 150px;
}
.wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 110px;
  background: url(data:image/png;base64,);
  background-size: 950px, 100px;
  background-repeat: repeat-x;
  -webkit-animation: waveBob 3s ease-in-out infinite;
          animation: waveBob 3s ease-in-out infinite;
}
@-webkit-keyframes waveBob {
  0%,
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
}
@keyframes waveBob {
  0%,
  100% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  50% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
}
.wave#wfront {
  opacity: 0.8;
  z-index: 4;
  background-position-x: 400px;
  -webkit-animation-delay: 0.25s;
          animation-delay: 0.25s;
}
.wave#wmid {
  opacity: 0.5;
  z-index: 3;
  background-position-x: 200px;
  -webkit-animation-delay: 0.5s;
          animation-delay: 0.5s;
}
.wave#wmid2 {
  opacity: 0.2;
  z-index: 2;
  background-position-x: 100px;
  -webkit-animation-delay: 0.75s;
          animation-delay: 0.75s;
}
.wave#wback {
  opacity: 0.7;
  z-index: 1;
  background-position-x: 300px;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

/* MOBILE */
@media (max-width: 992px) {
  .landingText {
    top: auto;
    font-size: 1.5em;
  }
  .landingText span {
    margin-top: 0;
    font-size: 2.5em;
  }
  .welcomeText {
    font-size: 1.5em;
  }
  #landing a {
    font-size: 1em;
  }
  #landing img#mountainsFront,
  #landing img#mountainsBack {
    bottom: 5vh;
  }
  #landing img#forestFront {
    bottom: 4vh;
  }
  #landing img#beach {
    height: 6vh;
  }
  #landing img#birdL,
  #landing img#birdR {
    bottom: -12vh;
  }
  #landing::before {
    height: 2vh;
  }
  .wave {
    height: 5vh;
    background-size: 400px, 100px;
  }
  /* mobile landscape mode */
  @media (max-height: 800px) {
    .landingText {
      top: 30%;
    }
    #landing img#birdL,
    #landing img#birdR {
      bottom: -60%;
    }
    #landing img#beach {
      height: 10vh;
    }
    .wave {
      height: 8.5vh;
    }
  }
}

#travelogue {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to bottom, #0059b3, #001a33);
  overflow: hidden;
}

.optionsEnabled {
  -webkit-transform: translateY(100px);
          transform: translateY(100px);
}
#searchbar.wrapper,
.pagination,
.postContainer {transition: -webkit-transform 0.5s ease-in-out;transition: transform 0.5s ease-in-out;transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;}
.appbar.options{
  position: absolute;
  top: 100px;
  opacity: 0;
  transition: 0.5s ease-in-out;
}
.appbar.options.enabled{
  opacity: 1;
}
.btn-leave {
  opacity: 1;
}

.btn-leave.example-leave-active {
  opacity: 0.01;
  transition: opacity 500ms ease-in;
}


.postContainer {
  padding-bottom: 100px;
  height: 100vh;
  width: 80vw;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  overflow-y: scroll;
  overflow-x: hidden;
}

#searchForm .formGroup .inputBox input { color: white }
#searchForm .formGroup .inputBox .line { background: #001a33 }
.dropdown {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 45px;
  background: #001a33;
  border-radius: 10px;
  cursor: pointer;
}
.dropdown span {
  position: absolute;
  font-weight: 500;
  letter-spacing: 0.5px;
  opacity: 1;
  transition: 0.5s ease-in-out;
}
.dropdown span.show { opacity: 0 }
.dropdown .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: white;
  width: 100px;
  height: 0;
  border-radius: 10px;
  opacity: 0;
  z-index: -1;
  transition: 0.5s ease-in-out;
}
.dropdown .content.show {
  height: 200px;
  opacity: 1;
  z-index: 1;
}
.dropdown .content span {
  position: relative;
  color: #001a33;
  margin: 5px;
}
#searchbar a {
  margin: 20px;
  text-decoration: none;
  color: #001a33;
  font-size: 2em;
  transition: 0.5s ease-in-out;
}
#searchbar a:hover { color: white }

.btn.paginate {
  padding: 0;
  margin: 40px 30px;
  background: none;
  border: none;
  color: #001a33;
  font-size: 2em;
  transition: 0.5s ease-in-out;
}
.btn.paginate:hover {
  color: white;
}
.btn.paginate:disabled {
  color: #494949;
}

#formOverlay {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
}

#fullOverlay {
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  width: 80%;
  background: linear-gradient(to bottom, #0059b3, #001a33);
  color: white;
}

@media (max-width: 992px) {
  .dropdown,
  .dropdown .content { width: 80px }
}
#postForm,
#authForm,
#commentForm,
#searchForm {
  width: 80%;
  padding: 20px;
  overflow-y: scroll;
}
#authForm {
  z-index: 10;
  color: white;
}
#searchForm {
  padding: 0;
  margin: 0;
  overflow-y: visible;
}
#commentForm {
  position: relative;
  width: 75vw;
}
#comment.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  width: 75vw;
}
#searchbar.wrapper{
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: white;
  width: 70%;
}
.formGroup {
  position: relative;
  width: 100%;
  padding: 20px 0;
}
.formGroup .formControl {
  position: relative;
  width: 100%;
  transition: 0.5s ease-in-out;
}
.formGroup .inputBox {
  position: relative;
  width: 100%;
  height: 40px;
}
.formGroup .inputBox input,
.formGroup .inputBox.textBox textarea {
  position: absolute;
  width: 100%;
  height: 100%;
  box-shadow: none;
  border: none;
  outline: none;
  font-size: 18px;
  padding: 0 10px;
  color: black;
  background: transparent;
  z-index: 4;
}
.formGroup .inputBox .placeholder {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 40px;
  font-size: 24px;
  padding: 0 10px;
  transition: 0.5s ease-in-out;
  pointer-events: none;
}
.formGroup .inputBox.textBox {
  position: relative;
  width: 100%;
  height: 100px;
  padding: 10px 0;
}
.formGroup .inputBox.textBox textarea {
  height: 100%;
  resize: none;
}
.formGroup .inputBox .line {
  position: absolute;
  display: block;
  bottom: 0;
  width: 100%;
  height: 2px;
  border-radius: 2px;
  transition: 0.5s ease-in-out;
  pointer-events: none;
  background: white;
}
.formGroup .textBox .line { bottom: -8% }
.formGroup a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 140px;
  height: 40px;
}
.formGroup button {
  background: transparent;
  border: none;
  position: absolute;
  padding: 0;
  margin: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 1.2em;
  letter-spacing: 0.7px;
  color: white;
}
.formGroup .inputBox input:focus + .placeholder,
.formGroup .inputBox input:not([value=""]) + .placeholder,
.formGroup .inputBox textarea:focus + .placeholder,
.formGroup .inputBox textarea:valid + .placeholder {
  top: -35px;
  left: -10px;
}
.formGroup .inputBox input:focus ~ .line,
.formGroup .inputBox input:not([value=""]) ~ .line,
.formGroup .inputBox textarea:focus ~ .line,
.formGroup .inputBox textarea:valid ~ .line { height: 100% }

.fileUpload {
  display: flex;
  flex-direction: column;
}

.fileUpload .fileCtrl a {
  text-decoration: none;
  margin-right: 30px;
  color: white;
  font-weight: 700;
}

@media (max-width: 992px) {
  .fileUpload .fileCtrl a {
    margin-right: 20px;
    font-size: 12px;
  }
}

h1 { font-family: "Rancho", cursive }

.card {
  position: relative;
  width: 300px;
  height: 250px;
  margin: 50px;
  padding: 20px 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: 0.5s ease-in-out;
  border-radius: 4px;
  background: linear-gradient(to bottom, #0059b3, #001a33);
  color: white;
}
.card:hover {
  height: 390px;
}
.card .cover {
  position: relative;
  width: 260px;
  height: 200px;
  top: -60px;
  border-radius: 4px;
  box-shadow: 0 0 10px black;
}
.card .cover img {
  width: 100%;
  height: 100%;
  border-radius: 4px;
}
.card .info {
  text-align: center;
  position: relative;
  margin-top: -140px;
  padding: 10px 15px;
  visibility: hidden;
  opacity: 0;
  transition: 0.5s ease-in-out;
  overflow: hidden;
}
.card:hover .info {
  visibility: visible;
  opacity: 1;
  margin-top: 0;
}

.info #author {
  font-size: 1em;
  max-height: 30px;
  overflow-y: scroll;
  margin-top: -10px;
  margin-bottom: 15px;
}
.info #author span {
  font-size: 1.5em;
  margin-left: 7px;
}
#author {
  font-size: 1.3em;
  max-height: 45px;
  overflow-y: scroll;
  margin: 0 10px 15px 10px;
}
#author span {
  font-size: 1.5em;
  margin-left: 15px;
}
.info .social,
.post.social .counts {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.info .social a,
.post.social .counts a {
  font-size: 35px;
  color: white;
  vertical-align: middle;
  margin-right: 10px;
}
.post.social .counts .count{
  margin: 0 100px;
  white-space: nowrap;
}
a .active{ color: red; }
a .inactive {color: #d3d3d3}

.title {
  overflow-x: hidden;
  overflow-y: scroll;
  word-break: break-all;
  margin-top: -50px;
  text-align: center;
  padding: 0;
}
.card .title {
  width: 250px;
  height: 80px; 
}
.card .title h1 {
  font-size: 3.3em;
  padding: 0;
  margin: 0;
}

.tags {
  margin: 10px 0;
  max-width: 280px;
  max-height: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  overflow-y: scroll;
}
.tags span {
  margin: 0 3px 4px 3px;
  padding: 2px 5px;
  border-radius: 20px;
  background: white;
  color: black;
  cursor: pointer;
}

.post.header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header .title {
  width: 100%;
  height: 120px;
  margin: 0;
}
.header .title h1 { font-size: 6em }
.header .tags {
  max-width: 100%;
}
.post.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 30px;
}

.content {
  margin: 0 15px;
}
.content p {
  font-size: 20px;
  max-height: 500px;
  overflow: scroll;
  font-weight: 500;
  opacity: 0.8;
}

.post.social {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.comments{
  width: 100%;
  height: 0;
  margin: 10px 0;
  padding: 0 20px;
  overflow-y: scroll;
  border: white dashed;
  transition: 0.5s ease-in-out;
}
.comments p {
  margin: 15px 0;
  border-bottom: white solid 1px;
  opacity: 0.8;
}
.comments.show{
  height: 200px;
}

.album {
  width: 100%;
  max-height: 600px;
  margin: 50px 0;
}

.album .albumReel {
  width: 100%;
  height: 500px;
}

.album .albumReel .swiper-slide img {
  height: 100%;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.album .albumThumbs {
  width: 100%;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
}
.album .albumThumbs .swiper-slide {
  opacity: 0.8;
}
.album .albumThumbs .swiper-slide img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}



@media (max-width: 992px) {
  .card {
    height: 390px;
  }
  .card .info {
    visibility: visible;
    opacity: 1;
    margin-top: 0px;
  }
  .post.social .counts .count{ margin: 0 50px }
  .album .albumReel .swiper-slide img {
    width: 100%;
    object-fit: contain;
  }
}
